import React, { useState, useEffect } from 'react';
import { Contact, NewContact, SearchFilters, Statistics as StatsType, ImportResult, PaginatedResponse } from './types';
import { qsoApi } from './api';
import ContactList from './components/ContactList.tsx';
import ContactForm from './components/ContactForm.tsx';
import SearchForm from './components/SearchForm.tsx';
import Statistics from './components/Statistics.tsx';
import ImportForm from './components/ImportForm.tsx';
import ExportForm from './components/ExportForm.tsx';
import AdminPage from './components/AdminPage.tsx';
import { 
  Radio, 
  Plus, 
  Search, 
  BarChart3, 
  Download,
  RefreshCw,
  Upload,
  Settings
} from 'lucide-react';
import './App.css';

function App() {
  const [contactsData, setContactsData] = useState<PaginatedResponse<Contact> | null>(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageSize, setPageSize] = useState(20);
  const [isSearchMode, setIsSearchMode] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState<'list' | 'add' | 'search' | 'stats' | 'import' | 'export' | 'admin'>('list');
  const [editingContact, setEditingContact] = useState<Contact | undefined>(undefined);

  const [searchFilters, setSearchFilters] = useState<SearchFilters>({});
  const [statistics, setStatistics] = useState<StatsType | null>(null);
  const [statsLoading, setStatsLoading] = useState(false);

  // Load contacts on component mount
  useEffect(() => {
    loadContacts(currentPage, pageSize);
  }, [currentPage, pageSize]);

  const loadContacts = async (page: number = currentPage, size: number = pageSize) => {
    setLoading(true);
    setError(null);
    setIsSearchMode(false);
    try {
      const data = await qsoApi.getContacts(page, size);
      setContactsData(data);
    } catch (err) {
      setError('Failed to load contacts: ' + (err instanceof Error ? err.message : 'Unknown error'));
    } finally {
      setLoading(false);
    }
  };

  const handleAddContact = async (newContact: NewContact) => {
    try {
      if (editingContact) {
        // Update existing contact
        await qsoApi.updateContact(editingContact.id, newContact);
        setEditingContact(undefined);
        // Reload current page to reflect changes
        await loadContacts(currentPage, pageSize);
      } else {
        // Create new contact
        await qsoApi.createContact(newContact);
        // Go to first page to see the new contact (assuming newest first)
        setCurrentPage(1);
        await loadContacts(1, pageSize);
      }
      setActiveTab('list');
      return editingContact ? 'updated' : 'created';
    } catch (err) {
      throw new Error(`Failed to ${editingContact ? 'update' : 'add'} contact: ` + (err instanceof Error ? err.message : 'Unknown error'));
    }
  };

  const handleEditContact = (contact: Contact) => {
    setEditingContact(contact);
    setActiveTab('add');
  };

  const handleCancelEdit = () => {
    setEditingContact(undefined);
    setActiveTab('list');
  };

  const handleDeleteContact = async (id: number) => {
    try {
      await qsoApi.deleteContact(id);
      // Reload current page to reflect changes
      if (isSearchMode) {
        await handleSearch({ ...searchFilters, page: currentPage, page_size: pageSize });
      } else {
        await loadContacts(currentPage, pageSize);
      }
    } catch (err) {
      setError('Failed to delete contact: ' + (err instanceof Error ? err.message : 'Unknown error'));
    }
  };

  const handleSearch = async (filters: SearchFilters) => {
    setLoading(true);
    setIsSearchMode(true);
    try {
      const filtersWithPagination = {
        ...filters,
        page: filters.page || currentPage,
        page_size: filters.page_size || pageSize,
      };
      const results = await qsoApi.searchContacts(filtersWithPagination);
      setContactsData(results);
      setSearchFilters(filters);
    } catch (err) {
      setError('Failed to search contacts: ' + (err instanceof Error ? err.message : 'Unknown error'));
    } finally {
      setLoading(false);
    }
  };

  const handleFiltersChange = (filters: SearchFilters) => {
    setSearchFilters(filters);
    handleSearch(filters);
  };

  const handleClearFilters = () => {
    setSearchFilters({});
    setCurrentPage(1);
    loadContacts(1, pageSize);
  };

  const handlePageChange = (page: number) => {
    setCurrentPage(page);
    if (isSearchMode) {
      handleSearch({ ...searchFilters, page, page_size: pageSize });
    } else {
      loadContacts(page, pageSize);
    }
  };

  const handlePageSizeChange = (newPageSize: number) => {
    setPageSize(newPageSize);
    setCurrentPage(1); // Reset to first page when changing page size
    if (isSearchMode) {
      handleSearch({ ...searchFilters, page: 1, page_size: newPageSize });
    } else {
      loadContacts(1, newPageSize);
    }
  };

  const handleRefresh = () => {
    if (isSearchMode) {
      handleSearch({ ...searchFilters, page: currentPage, page_size: pageSize });
    } else {
      loadContacts(currentPage, pageSize);
    }
  };

  const loadStatistics = async () => {
    setStatsLoading(true);
    try {
      const stats = await qsoApi.getStatistics();
      setStatistics(stats);
    } catch (err) {
      setError('Failed to load statistics: ' + (err instanceof Error ? err.message : 'Unknown error'));
    } finally {
      setStatsLoading(false);
    }
  };

  // Load statistics when switching to stats tab
  useEffect(() => {
    if (activeTab === 'stats' && !statistics) {
      loadStatistics();
    }
  }, [activeTab, statistics]);

  const handleImportComplete = (result: ImportResult) => {
    if (result.success) {
      setError(null);
      // Refresh contacts after successful import
      loadContacts();
      // Show success message
      alert(`Import completed successfully!\n\nImported: ${result.imported_count} contacts\nSkipped: ${result.skipped_count} contacts\nErrors: ${result.error_count} contacts`);
      // Switch back to list view
      setActiveTab('list');
    } else {
      setError(`Import failed: ${result.message}\n${result.errors.join('\n')}`);
    }
  };

  const TabButton = ({ 
    tab, 
    icon: Icon, 
    label, 
    isActive 
  }: { 
    tab: typeof activeTab, 
    icon: React.ComponentType<any>, 
    label: string, 
    isActive: boolean 
  }) => (
    <button
      onClick={() => {
        setActiveTab(tab);
        // Clear editing state when navigating to a different tab
        if (tab !== 'add' && editingContact) {
          setEditingContact(undefined);
        }
      }}
      className={`sidebar-button ${isActive ? 'active' : ''}`}
    >
      <Icon size={20} />
      <span>{label}</span>
    </button>
  );

  return (
    <div className="app">
      <header className="header">
        <div className="header-content">
          <div className="logo">
            <Radio size={32} />
            <h1>GoQSO</h1>
          </div>
          <div className="header-actions">
            <button onClick={handleRefresh} className="refresh-btn" disabled={loading}>
              <RefreshCw size={16} className={loading ? 'spinning' : ''} />
              Refresh
            </button>
          </div>
        </div>
      </header>

      <div className="app-body">
        <nav className="sidebar">
          <div className="sidebar-nav">
            <TabButton tab="list" icon={Radio} label="QSO Log" isActive={activeTab === 'list'} />
            <TabButton tab="add" icon={Plus} label={editingContact ? "Edit QSO" : "Add QSO"} isActive={activeTab === 'add'} />
            <TabButton tab="search" icon={Search} label="Search" isActive={activeTab === 'search'} />
            <TabButton tab="stats" icon={BarChart3} label="Statistics" isActive={activeTab === 'stats'} />
            <TabButton tab="import" icon={Upload} label="Import" isActive={activeTab === 'import'} />
            <TabButton tab="export" icon={Download} label="Export" isActive={activeTab === 'export'} />
            <TabButton tab="admin" icon={Settings} label="Admin" isActive={activeTab === 'admin'} />
          </div>
        </nav>

        <main className="main-content">
          {error && (
            <div className="error-message">
              {error}
              <button onClick={() => setError(null)}>×</button>
            </div>
          )}

          {activeTab === 'list' && (
            <ContactList 
              data={contactsData}
              loading={loading}
              onDelete={handleDeleteContact}
              onEdit={handleEditContact}
              onPageChange={handlePageChange}
              onPageSizeChange={handlePageSizeChange}
            />
          )}

          {activeTab === 'add' && (
            <ContactForm 
              onSave={handleAddContact} 
              onCancel={handleCancelEdit}
              editingContact={editingContact}
            />
          )}

          {activeTab === 'search' && (
            <div>
              <SearchForm 
                filters={searchFilters}
                onFiltersChange={handleFiltersChange}
                onClearFilters={handleClearFilters}
                totalContacts={contactsData?.total_items || 0}
                filteredContacts={contactsData?.total_items || 0}
              />
              <ContactList 
                data={contactsData}
                loading={loading}
                onDelete={handleDeleteContact}
                onEdit={handleEditContact}
                onPageChange={handlePageChange}
                onPageSizeChange={handlePageSizeChange}
              />
            </div>
          )}

          {activeTab === 'stats' && (
            <Statistics 
              statistics={statistics || {
                total_qsos: 0,
                unique_callsigns: 0,
                unique_countries: 0,
                confirmed_qsos: 0,
                qsos_by_band: {},
                qsos_by_mode: {},
                qsos_by_country: {},
                bands_worked: {},
                modes_used: {},
                countries_worked: {},
                date_range: { earliest: '', latest: '' }
              }}
              loading={statsLoading}
            />
          )}

          {activeTab === 'import' && (
            <ImportForm onImportComplete={handleImportComplete} />
          )}

          {activeTab === 'export' && (
            <ExportForm onError={setError} />
          )}

          {activeTab === 'admin' && (
            <AdminPage onError={setError} />
          )}
        </main>
      </div>
    </div>
  );
}

export default App;